<template>
 <div id="app">
  <a href="./index.html#/welcome" class="goback" v-show="$route.path !== '/' && $route.path !== '/welcome'"> ← BACK </a>
      <router-view
      :resizeObj="resizeObj"
      :mousemoveObj="mousemoveObj"
      :clickObj="clickObj">
    </router-view>
 </div>
</template>

<script>
// import axios from "axios";
export default {
  name: 'IndexComponent',
  components: {
  },
  watch: {
  },
  data() {
    return {
      resizeObj: null,
      mousemoveObj: {},
      clickObj: {}
    }
  },
  methods: {
    disableCopying() {
      document.addEventListener('contextmenu', event => event.preventDefault());
    }
  },
  mounted() {
    this.disableCopying();
    window.addEventListener("resize", (e) => {
      this.resizeObj = e;
    });
    document.addEventListener('mousemove', e => {
      this.mousemoveObj = {
        target: e.target,
        move: {
          x: e.clientX,
          y: e.clientY
        }
      }
    });
    document.addEventListener('mousedown', (e) => {
      this.clickObj = {
        target: e.target
      }
    });

    window.addEventListener('error', function (e) {
      // prevent React's listener from firing
      e.stopImmediatePropagation();
        // prevent the browser's console error message
      e.preventDefault();
    });
  }
}
</script>

