import Vue from "vue";
import Vuex from "vuex";
// import createPersistedState from "vuex-persistedstate";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    videoUrl: null,
    prismicData: []
  },

  mutations: {
    setVideoUrl(state, videoUrl) {
      Vue.set(state, "videoUrl", videoUrl);
    },
    setPrismicData(state, prismicData) {
      Vue.set(state, "prismicData", prismicData);
    }
  },

  actions: {

  },
//   plugins: [createPersistedState()],
});
