import Vue from "vue";
var PrismicDOM = require("prismic-dom");

// import Vue from "vue";
import * as p from '@prismicio/client';
let accessToken = process.env.VUE_APP_BGASSETSKEY;


export default new class Prismic {
  constructor() {
  }
  getData() {
    return new Promise((resolved) => {

        let client = p.createClient("https://vayabrutal.cdn.prismic.io/api/v2", { 
            accessToken
        })
        
        client.getSingle('assets').then((assets) => {
            resolved(assets.data);
        })
      
    });
  }
}

Vue.prototype.$cms = new (class CMSModule {
  text(field) {
    return PrismicDOM.RichText.asText(this.fields[field]);
  }

  textField(field) {
    return PrismicDOM.RichText.asText(field);
  }

  html(field) {
    return PrismicDOM.RichText.asHtml(this.fields[field]);
  }

  htmlField(field) {
    return PrismicDOM.RichText.asHtml(field);
  }
})();
