import Vue from "vue";
import VueRouter from "vue-router";

// Because Vue is not global we tie Vue
// and VueRouter together manually
Vue.use(VueRouter);

// Initialize router
const router = new VueRouter({
  base: "/",
  // mode: "history"
  mode: "hash",
  routes: [
    {
      path: "",
      component: () => import("./components/LandingPage.vue"),
    },
    {
      path: "/welcome",
      component: () => import("./App.vue"),
    },
    {
      path: "/live",
      component: () => import("./components/Live.vue"),
    },
    {
      path: "/livevideo",
      component: () => import("./components/LiveVideo.vue"),
    },
    {
      path: "/pulsevideo",
      component: () => import("./components/PulseVideo.vue"),
    },
    {
      path: "/sexualvideo",
      component: () => import("./components/SexualVideo.vue"),
    },
    {
      path: "/vinylevideo",
      component: () => import("./components/VinylVideo.vue"),
    },
    {
      path: "/projectionvideo",
      component: () => import("./components/ProjectionVideo.vue"),
    },
  ],
});

export default router;

